<template>
	<div class="cartcontrol">
		<transition name="fade">
			<div class="cart-decrease" v-show="food.count" @click.stop.prevent="deleaseCart">
				<span class="iconfont icon-jianhao"></span>
			</div>
		</transition>
		<div class="cart-count" v-show="food.count>0">{{food.count}}</div>
		<div class="cart-add iconfont icon-jiahao" @click.stop.prevent="addCart"></div>
	</div>
</template>
<script>
	import Vue from 'vue'
	export default{
		props:['food'],
		methods:{
			addCart(e){
				if(!e._constructed){
					return
				}
				if(!this.food.count){
					Vue.set(this.food,'count',1)
				}else{
					this.food.count++;
				}
			},
			deleaseCart(e){
				if(!e._constructed){
					return
				}
				if(this.food.count){
					this.food.count--;
				}
			}
		}
	}
</script>
<style lang="less" scoped>
	.cartcontrol{
		.fade-enter-active{
			transition: all .5s;
		}
		.fade-leave-active{
			transition: all .5s;
		}
		.fade-enter{
			transform:translateX(100%) rotate(180deg);
			opacity:0;
		}
		.fade-leave-to{
			transform:translateX(100%) rotate(180deg);
			opacity:0;
		}
		.iconfont{
			color:rgb(0,160,220);
			font-size: .36rem;
		}
		div{
			display: inline-block;
		}
		.cart-count{
			width: .4rem;
			text-align: center;
		}
	}
</style>


