<template>
	<div class="star" :class="starSize">
		<span v-for="(star,i) in stars" :class="star" class="star-item">
		</span>
	</div>
</template>
<script>
	const LENGTH = 5;
	const STAR_ON = 'on';
	const STAR_OFF = 'off';
	const STAR_HALF = 'half';
	export default{
		props:{
			size:{type:Number},
			score:{type:Number}
		},
		computed:{
			starSize(){
				return "star-"+this.size;
			},
			stars(){
				var starArr=[];
				for(let i = 0; i < parseInt(this.score); i++){
					starArr[i]=STAR_ON;
				}
				if(this.score%1 >= 0.5){
					starArr.push(STAR_HALF);
				}
				for(var i=0;i<LENGTH;i++){
					if(starArr.length<5){
						starArr.push(STAR_OFF);
					}
				}
				return starArr;
			}
		}
	}
</script>
<style lang="less" scoped>
	.star{
		&.star-12{
			.star-item{
				width: .24rem;
				height: .24rem;
				display: inline-block;
				margin:.02rem 0 .12rem;
				&:last-child{
					margin-right: 0;
				}
				&.on{
					background: url('../../../static/imgs/star_on.png');
					background-size: cover;
				}
				&.off{
					background: url('../../../static/imgs/star_off.png');
					background-size: cover;
				}
				&.half{
					background: url('../../../static/imgs/star_half.png');
					background-size: cover;
				}
			}
		}
		&.star-24{
			.star-item{
				width: .36rem;
				height: .36rem;
				display: inline-block;
				margin: .16rem .1rem .36rem 0;
				&:last-child{
					margin-right: 0;
				}
				&.on{
					background: url('../../../static/imgs/star_on.png');
					background-size: cover;
				}
				&.off{
					background: url('../../../static/imgs/star_off.png');
					background-size: cover;
				}
				&.half{
					background: url('../../../static/imgs/star_half.png');
					background-size: cover;
				}
			}
		}
		&.star-36{
			.star-item{
				width: .3rem;
				height: .3rem;
				display: inline-block;
				margin-right: .06rem;
				&:last-child{
					margin-right: 0;
				}
				&.on{
					background: url('../../../static/imgs/star_on.png');
					background-size: cover;
				}
				&.off{
					background: url('../../../static/imgs/star_off.png');
					background-size: cover;
				}
				&.half{
					background: url('../../../static/imgs/star_half.png');
					background-size: cover;
				}
			}
		}
		&.star-48{
			.star-item{
				width: .48rem;
				height: .48rem;
				display: inline-block;
				margin-right: .48rem;
				&:last-child{
					margin-right: 0;
				}
				&.on{
					background: url('../../../static/imgs/star_on.png');
					background-size: cover;
				}
				&.off{
					background: url('../../../static/imgs/star_off.png');
					background-size: cover;
				}
				&.half{
					background: url('../../../static/imgs/star_half.png');
					background-size: cover;
				}
			}
			
		}
	}
</style>


