<template>
	<div class="header">
		<div class="background-img">
			<img :src="seller.avatar" alt="粥品香坊">
		</div>
		<div class="header-top clearfix">
			<div class="sellerImg">
				<img :src="seller.avatar" alt="粥品香坊">
			</div>
			<div class="sellerInfo">
				<div class="brandInfo">
					<img src="../../../static/imgs/brand.png" alt="">
					<span>{{seller.name}}</span>
					<p>{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
				</div>
				<div class="supports">
					<img src="../../../static/imgs/decrease.png" alt="">
					<span v-if="seller.supports">{{seller.supports[0].description}}</span>
					<div class="supports-list" @click="showDetails">
						<span v-if="seller.supports">{{seller.supports.length}}个 <i class="el-icon-arrow-right"></i></span>
					</div>
				</div>
			</div>
		</div>
		<div class="header-bottom">
			<div class="announcement">
				<img src="../../../static/imgs/bulletin.png" />
				<span>{{seller.bulletin}}</span>
				<i class="el-icon-arrow-right"></i>
			</div>
		</div>
		<transition name="fade">
			<div v-show="detailShow" class="sellerDetail">
				<div class="detail-wrapper">
					<h1 class="name">{{seller.name}}</h1>
					<div class="star-wrapper">
						<Star :size="48" :score="seller.score"></Star>
					</div>
					<div class="title">
						<div class="line"></div>
						<div class="text">优惠信息</div>
						<div class="line"></div>
					</div>
					<ul v-if="seller.supports" class="supprots">
						<li class="support-item" v-for="(v,i) in seller.supports">
							<span class="icon" :class="classMap[v.type]"></span>
							<span>{{v.description}}</span>
						</li>
					</ul>
					<div class="title">
						<div class="line"></div>
						<div class="text">商家公告</div>
						<div class="line"></div>				
					</div>
					<div class="bulletin">
						<p class="content">{{seller.bulletin}}</p>
					</div>
				</div>
				<i @click="closeDetails" class="el-icon-close close-btn"></i>
			</div>
		</transition>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex' 
	import Star from '../star/star.vue'
	export default{
		computed:{
			...mapGetters([
				'seller',
				'detailShow'
			])
		},
		components:{
			Star
		},
		created(){
			this.$store.dispatch('getSeller');
			this.classMap=['decrease','discount','special','invoice','guarantee']
		},
		methods:{
			showDetails(){
				this.$store.dispatch('showSellerInfo');
			},
			closeDetails(){
				this.$store.dispatch('hideSellerInfo');
			}
		}
	}
</script>
<style lang='less' scoped>
	.header{
		height: 2.68rem;
		width: 7.5rem;
		background: rgba(7,17,27,.5);
		position: relative;
		.header-top{
			padding: 0.48rem 0 0 0.48rem;
		}
		.background-img{
			filter: blur(5px);
			z-index: -3;
			position: absolute;
			left:0;top:0;
			right:0;
			bottom:0;
			img{
				width: 100%;
				height: 100%;
			}
		}
		.sellerImg{
			float:left;
		}
		.sellerImg img{
			width: 1.28rem;
			border-radius: .04rem;
		}
		.sellerInfo{
			margin-left: 0.32rem;
			float:left;
				.brandInfo img{
					width: .6rem;
					height: .36rem;
					vertical-align: top;
				}
				span{
					font-size: .32rem;
					color: #fff;
					font-weight: bold;
					line-height: .36rem;
					vertical-align: top;
					margin-left: .12rem;
				}
				p{
					margin: .16rem 0 .2rem;
					font-size: .24rem;
					line-height: .24rem;
					font-weight: 200;
					color: #fff;
				}
				.supports{
					position: relative;
				}
				.supports img{
					width: .32rem;
					vertical-align: center;
				}
				.supports span{
					margin-left: .08rem;
					font-size: .2rem;
					line-height: .32rem;
				}
				.supports-list{
					position: absolute;
					top:-.08rem;right:-1.5rem;
					display: inline-block;
					width: .86rem;
					color: #fff;
					height: .46rem;
					text-align: center;
					border-radius: 40px;
					background: rgba(0,0,0,.2);
					height: .46rem;
					span{
						line-height: .46rem;
						i{
							line-height: .46rem;
						}
					}
				}
		}
		.header-bottom{
			position: absolute;
			bottom:0;
			width: 100%;
			height: .56rem;
			background: rgba(7,17,27,0.2);
			.announcement{
				margin: .16rem 0 0 .24rem;
				color: #fff;
				img{
					width: .44rem;
					height: .24rem;
					vertical-align: top;
				}
				span{
					display:inline-block;
					width: 6rem;
					white-space: nowrap;
					text-overflow:ellipsis;
					overflow: hidden;
					font-size: .2rem;
					padding-top: .02rem;
					margin:0 .08rem;
				}
				i{	
					float: right;
					margin-right: .24rem;
					line-height: .3rem;
				}
			}
		}
	/* 商家显示隐藏 */
	.fade-enter-active,.fade-leave-active{
		transition: opacity .8s;
	}
	.fade-enter,.fade-leave-to{
		opacity:0;
	}
	.sellerDetail{
		z-index: 9;
		position: fixed;
		top:0;
		left:0;
		width: 7.5rem;
		height: 100%;
		background: rgba(7,17,27,0.8);
		color: #fff;
		.name{
			margin-top: 1.28rem;
			font-size: .32rem;
			line-height: .32rem;
			font-weight: 700;
			text-align:center;
		}
		.star-wrapper{
			height: .48rem;
			margin: .32rem .56rem;
			text-align:center;
		}
		.title{
			display: flex;
			margin:.56rem .72rem .48rem .72rem;
			.line{
				flex: 1; 
				position: relative;
	            top: -.1rem;
	            border-bottom: 2px solid rgba(255, 255, 255, 0.2);
			}
			.text{
				font-size: .28rem;
				font-weight:400;
				padding:0 .24rem;
			}
		}
		.support-item{
			margin-bottom: .24rem;
			font-size: .24rem;
			font-weight: 200;
			margin-left: .96rem;
			.icon{
				display: inline-block;
				width: .32rem;
				height: .32rem;
				vertical-align: bottom;
				margin-right: .12rem;
				&.decrease{
					background: url('../../../static/imgs/decrease.png');
					background-size: cover;
				}
				&.discount{
					background: url('../../../static/imgs/discount.png');
					background-size: cover;
				}
				&.guarantee{
					background: url('../../../static/imgs/guarantee.png');
					background-size: cover;
				}
				&.special{
					background: url('../../../static/imgs/special.png');
					background-size: cover;
				}
				&.invoice{
					background: url('../../../static/imgs/invoice.png');
					background-size: cover;
				}
			}
		}
		.bulletin{
			margin: 0 .96rem;
			line-height: .48rem;
			font-size: .24rem;
			font-weight: 200;
		}
		.close-btn{
			position:absolute;
			bottom: .64rem;
			left:50%;
			margin-left: -.26rem;
			font-size: .54rem;
			color: rgba(255,255,255,0.5);
		}
	}
	}
</style>


