import types from '../types.js'
import axios from 'axios'

//data
const state = {
	ratings:[],
	selectType:2,
	selectRatings:[],
	onlyContent:false,
	types:['全部','满意','不满意']
}
//返回数据
const getters = {
	ratings(state){
		return state.ratings
	},
	selectType(state){
		return state.selectType
	},
	selectType(state){
		return state.selectType
	},
	selectRatings(state){
		return state.selectRatings
	},
	onlyContent(state){
		return state.onlyContent
	},
	types(state){
		return state.types
	}

}

//接受方法提交给mutations执行
const actions = {
	getRatings({commit,state}){
		axios.get('../../../static/data.json').then((res)=>{
			console.log(res.data);
			if(res.data.errno === 0){
				commit(types.GET_RATINGS,res.data.ratings);
			}
		});
	},
	showRating({commit},type){
		commit(types.SELECT,type)
	},
	toggles({commit}){
		commit(types.TOGGLE_CONTENT)
	},
	resetType({commit}){
		commit(types.RESET_TYPES)
	}
}

const mutations={
	[types.RESET_TYPES](state,type){
		state.selectType=2;
		state.selectRatings=state.ratings;
	},
	[types.GET_RATINGS](state,data){
		state.ratings=data;
	},
	[types.TOGGLE_CONTENT](state){
		state.onlyContent = !state.onlyContent;
		state.selectRatings=state.ratings.filter(rating=>{
			if(state.onlyContent && !rating.text){
				return false;
			}else if(state.selectType === 2){
				return rating.text
			}else{
				return state.selectType === rating.rateType
			}
		})
	},
	[types.SELECT](state,type){
		state.selectType=type;
		state.selectRatings=state.ratings.filter(rating=>{
			if(state.onlyContent && !rating.text){
				return false;
			}else if(state.selectType === 2){
				return true
			}else{
				return state.selectType === rating.rateType
			}
		})
	}
}

export default{
	state,
	getters,
	actions,
	mutations
}


