<template>
	<div class="seller" ref="seller">
	<div>
		<div class="seller-detail">
			<div class="name-ratings">
				<h2>{{seller.name}}</h2>
				<div>
					<Star class="star" :size="24" :score="seller.score"></Star>
					<span class="rating">( {{seller.ratingCount}} )</span>
					<span class="sellCount">月售{{seller.sellCount}}单</span>
				</div>
			</div>
			<div class="collect">
				<span @click="collect" :class="classMap[Number(collectImg)]"></span>
				<p>{{collectInfo}}</p>
			</div>
			<div class="minPrice">
				<div>
					<p class="min-title">起送价</p>
					<p><span class="price-info">{{seller.minPrice}}</span>元</p>
				</div>
				<div>
					<p class="min-title">商家配送费</p>
					<p><span class="price-info">{{seller.deliveryPrice}}</span>元</p>
				</div>
				<div>
					<p class="min-title">平均配送时间</p>
					<p><span class="price-info">{{seller.deliveryTime}}</span>分钟</p>
				</div>
			</div>
		</div>
		<Split></Split>
		<div class="seller-bulletin">
			<h1>公告与活动</h1>
			<p>{{seller.bulletin}}</p>
			<div>
				<ul v-if="seller.supports" class="supports">
					<li class="support-item" v-for="(v,i) in seller.supports">
						<span class="icon" :class="classMap2[v.type]"></span>
						<span>{{v.description}}</span>
					</li>
				</ul>
			</div>
		</div>
		<Split></Split>
		<div class="seller-img clearfix">
			<h1>商家实景</h1>
			<div class="imglist" ref="picWrapper">
				<ul>
					<li v-for="pic in seller.pics">
						<img :src="pic" alt="">
					</li>
				</ul>
			</div>
		</div>
		<Split></Split>
		<div class="seller-info">
			<h1>商家信息</h1>
			<div>
				<ul class="supports">
					<li class="support-item" v-for="(item,index) in seller.infos">
						<span>{{item}}</span>
					</li>
				</ul>
			</div>
		</div>
	</div>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import Star from '../star/star.vue'
	import Split from '../split/split.vue'
	import BScroll from 'better-scroll'
	export default{
		computed:{	
			...mapGetters([
					'seller',
					'collectImg',
					'collectInfo'
			])
		},
		components:{
			Star,
			Split
		},
		methods:{
			collect(){
				this.$store.dispatch('getCollect');
			},
			_initseller(){
				this.sellerwrap = new BScroll(this.$refs.seller,{
					click:true
				})
			},
			_initsellerimg(){
				this.picScroll = new BScroll(this.$refs.picWrapper, {
                scrollX: true,
                eventPassthrough: 'vertical',
                click:true
              });
			}
		},
		created(){
			this.$store.dispatch('getSeller');
			this.classMap=['iconfont icon-xin','iconfont icon-xin1'];
			this.classMap2=['decrease','discount','special','invoice','guarantee'];
			this.$nextTick(()=>{
				setTimeout(()=>{
					this._initseller();
					this._initsellerimg();
				},100)
			})
		}
	}
</script>
<style lang="less" scoped>
	.seller{
		height: 9.69rem;
		overflow: hidden;
		.seller-detail{
			margin: .36rem;
			.name-ratings{
				margin-right: .8rem;
				float: left;
				.star{
					float: left;
				}
				.sellCount,.rating{
					margin-left: .16rem;
					line-height: .78rem;
					color: rgb(77,85,93);
					font-size: .2rem;
				}
				h2{
					font-size: .3rem;
					line-height: .28rem;
					color: rgb(7,17,27);
				}
			}
			.collect{
				text-align: center;
				.icon-xin{
					font-size: .48rem;
				}
				.icon-xin1{
					font-size: .48rem;
					color: red;
				}
			}
			.minPrice{
				border-top:1px solid rgba(7,17,27,0.1);
				clear: both;
				display: flex;
				text-align:center;
				padding-top: .36rem;
				div{
					flex:1;
					border-right: 1px solid rgba(7,17,27,0.1);
					&:last-child{
						border-right: none;
					}
				}
				.min-title{
					color: rgb(147,153,159);
					font-size: .2rem;
					line-height: .2rem;margin-bottom: .16rem;
				}
				.price-info{
					font-size: .48rem;
					font-weight: 200;
					line-height: .48rem;
					color:rgb(7,17,27);
				}
			}
		}
		.seller-bulletin,.seller-info{
			padding: .36rem;
			h1{
				color:rgb(7,17,27);
				font-size: .3rem;	
				margin-bottom: .16rem;
			}
			p{
				padding:0 .24rem .32rem;
				font-size: .24rem;
				line-height: .48rem;
				color: rgb(240,20,20);
			}
			.supports{
				li{
					border-top:1px solid rgba(7,17,27,0.1);
					padding: .32rem 0;
					&:last-child{
						padding-bottom: 0;
					}
				}
				.icon{
					display: inline-block;
					width: .42rem;
					height: .42rem;
					vertical-align: middle;
					margin-right: .12rem;
					&.decrease{
						background: url('../../../static/imgs/decrease.png');
						background-size: cover;
					}
					&.discount{
						background: url('../../../static/imgs/discount.png');
						background-size: cover;
					}
					&.guarantee{
						background: url('../../../static/imgs/guarantee.png');
						background-size: cover;
					}
					&.special{
						background: url('../../../static/imgs/special.png');
						background-size: cover;
					}
					&.invoice{
						background: url('../../../static/imgs/invoice.png');
						background-size: cover;
					}
				}
			}

		}
		.seller-img{			
			padding: .36rem;
			h1{
				color:rgb(7,17,27);
				font-size: .3rem;	
				margin-bottom: .16rem;
			}
			.imglist{
					height: 1.8rem;
					overflow: hidden;
				ul{
					width: 10.1rem;
					li{
						float: left;
						img{
							width: 2.4rem;
							height: 1.8rem;
							margin-right: .12rem;
						}
					}
				}
			}
		}
		.seller-info{
			padding-bottom: 1rem;

		}

	}
</style>


