<template>
	<div class="ratings" ref="ratingsWrap">
	<div>
		<div class="ratingsInfo">
			<div class="score">
				<h1>{{seller.score}}</h1>
				<h2>综合评分</h2>
				<p>高于周边商家{{seller.rankRate}}%</p>
			</div>
			<div class="star-wrapper">
				<div class="star-score">
					<span>服务态度</span>
					<Star :size="36" :score="seller.serviceScore"></Star>	
					<span class="score-num">{{seller.serviceScore}}</span>				
				</div>
				<div class="star-score">
					<span>商品评分</span>
					<Star :size="36" :score="seller.foodScore"></Star>	
					<span class="score-num">{{seller.foodScore}}</span>				
				</div>
				<div class="star-score">
					<span>送达时间</span>	
					<span class="delivaryTime">{{seller.deliveryTime}}分钟</span>				
				</div>
			</div>
		</div>
		<Split></Split>
		<div class="ratings-content">
			<selectRating></selectRating>
			<div class="user-ratings">
				<ul>
					<li v-for="rating in selectRatings">
						<div class="user-top">
							<img :src="rating.avatar" alt="">
							<div class="star-wrap">
								<span>{{rating.username}}</span>
								<div class="user-star">
									<Star :size="12" :score="rating.score"></Star>
									<span>{{rating.deliveryTime}}分钟送达</span>
								</div>
							</div>
							<span>{{rating.rateTime | date}}</span>
						</div>
						<div class="user-rating">
							<p>{{rating.text}}</p>
							<div>
								<i :class="classMap[rating.rateType]" ></i>
								<span v-for="v in rating.recommend">
									{{v}}
								</span>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>	
	</div>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import Star from '../star/star.vue'
	import Split from '../split/split.vue'
	import BScroll from 'better-scroll'
	import selectRating from '../ratingselect/ratingselect.vue'
	export default{
		computed:{
			...mapGetters([
				'seller',
				'selectRatings'
			])
		},
		components:{
			Star,
			Split,
			selectRating
		},
		watch:{
			selectRatings(){
				this.$nextTick(()=>{
					if(!this.ratingsinfo){
						this.ratingsinfo = new BScroll(this.$refs.ratingsWrap,{
							click:true
						});
					}else{
						this.ratingsinfo.refresh();
					}
				});
			}
		},
		created(){
			this.classMap=['iconfont icon-damuzhion','iconfont icon-down'];
			
			setTimeout(()=>{
				this.$store.dispatch('resetType');
					this.$nextTick(()=>{
						this.ratingsinfo = new BScroll(this.$refs.ratingsWrap,{
							click:true
						});
					})
			},10)
		},
		filters:{
			date:function(value){
				function toDouble(n){
					return n<10?'0'+n: n;
				}
				var d=new Date(value);
				return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+' '+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
			}
		}
	}
</script>
<style lang="less" scoped>
	.ratings{
		width: 7.5rem;
		height: 9.69rem;
		overflow: hidden;
		.ratingsInfo{
			display: flex;
			margin: .36rem 0;
			text-align: center;
			.score{
				flex: 0 0 2.75rem;
				border-right: 1px solid rgba(7,17,27,0.1);
				h1{
					color: rgb(255,153,0);
					font-size: .48rem;
					line-height: .48rem;
				}
				h2{
					color: rgb(7,17,27);
					font-size: .24rem;
					line-height: .24rem;
					margin: .12rem 0 .16rem;
				}
				p{
					color: rgba(7,17,27,0.4);
					font-size: .2rem;
					line-height: .2rem;
				}
			}
			.star-wrapper{
				flex: 0 0 4.74rem;
				box-sizing: border-box;
				padding: 0 .24rem;
				.star-score{
					display: flex;
					span{
						font-size: .24rem;
						padding: .12rem .24rem;
					}
					.score-num{
						color: rgb(255,153,0);
					}
					.star{
						margin-top: .08rem;
						font-size: .24rem;
					}
					.delivaryTime{
						color:rgb(147,153,159);
					}
				}
			}
		}
		.ratings-content{
			.user-ratings{
				margin:0 .36rem;
				li{
					padding: .36rem 0;
					border-bottom: 1px solid rgba(7,17,27,0.1);
					&:last-child{
						border: none;
					}
					.user-top{
						display: flex;
						.star-wrap{
						}
						.star-wrap > span{
								color: rgb(7,17,27); 
								font-size: .2rem;
								line-height: .24rem;
						}
						.user-star{
							display: flex;
							span{
								margin-top: .04rem;
								color: rgb(147,153,159);
								margin-left: .12rem;
								font-weight: 200;
								line-height: .24rem;
							}
						}
						img{
							width: .56rem;
							height: .56rem;
							margin-right: .24rem;
							border-radius: 50%;
						}
						div{
						}
						span{
							text-align: right;
							color: rgb(147,153,159);
							flex:1;
						}
					}
					.user-rating{
						margin-left: .8rem;
						.icon-damuzhion{
							color: rgb(0,160,220);
						}
						.icon-down{
							color: rgb(183,187,191);
						}
						p{
							font-size: .24rem;
							line-height: .36rem;
							margin-bottom: .16rem;
						}
						span{
							margin: 0 .08rem;
							border: 1px solid rgba(7,17,27,0.1);
							padding: .06rem .12rem;
							font-size:.18rem;
							line-height: .42rem;
							color:rgb(147,153,159);
						}
					}
				}
			}
		}
	}
</style>


