<template>
	<div class="shopcar">
		<div class="content-left">
			<div class="icon-car">
				<div @click="showList" class="logo" :class="{'goods':totalPrice}">
					<i class="iconfont icon-gouwuche" :class="{'icon':totalPrice}"></i>
				</div>
				<div class="num" v-show="goodsCount">
					{{goodsCount}}
				</div>
			</div>
			<div class="total-price" :class="{'price':totalPrice}">
				￥{{totalPrice}}
			</div>
			<div class="delivery-price">
				另需配送费￥{{seller.deliveryPrice}}元
			</div>
		</div>
		<div class="content-right">		
			<div class="min-price" @click="show" :class="{'pay':totalPrice>=seller.minPrice}">
				{{deliveryPrice}}
			</div>
		</div>
		<transition name="top">
			<div class="goods-list" v-show="listShow">
				<div class="list-title">
					<span class="title">购物车</span>		
					<span class="clear" @click="clear">清空</span>
				</div>
				<div class="foods-list" ref="goodsWrap">
					<ul>
						<li class="food-list" v-for="food in selectFoods">						
							<span class="title">{{food.name}}</span>
							<div class="control-num">
								<span class="price">￥{{food.price}}</span>
								<cartcontrol :food="food"></cartcontrol>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</transition>
		<transition name="show">
			<div class="list-mask" @click="showList" v-show="listShow">
				
			</div>
		</transition>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import cartcontrol from '../cartcontrol/cartcontrol.vue'
	import BScroll from 'better-scroll'
	export default{
		data(){
			return{
				fold:true
			}
		},
		computed:{
			...mapGetters([
				'seller',
				'goods',
				'foods'
			]),
			selectFoods(){
				let foods = [];
				this.goods.forEach((good)=>{
					good.foods.forEach((food)=>{
						if(food.count>0){
							foods.push(food);
						}
					})
				})
				return foods;
			},
			totalPrice(){
				let total = 0;
				this.selectFoods.forEach(food=>{
					total+=food.price*food.count;
				})
				return total;
			},
			goodsCount(){
				let total = 0;
				this.selectFoods.forEach(food=>{
					total += food.count
				});
				return total;
			},
			deliveryPrice(){
				if(this.totalPrice==0){
					return `￥${this.seller.minPrice}起送`
				}else if(this.totalPrice<this.seller.minPrice) {
					let diff = this.seller.minPrice-this.totalPrice
					return `还差${diff}起送`
				}else{
					return "结算"
				}
			},
			listShow(){
				if(!this.totalPrice){
					this.fold=true;
					return false;
				}
				let show = !this.fold;
		        return show;

			}
		},
		components:{
			cartcontrol
		},
		created(){
			this.$store.dispatch('getSeller');	
					
		},
		methods:{
			showList(){
				if(!this.totalPrice){
					return;
				}
				this.fold=!this.fold;
			    if(!this.fold){
					setTimeout(() => {
						if(!this.goodss){
							this.goodss= new BScroll(this.$refs.goodsWrap,{
								click:true
							});								
						}else{
							this.goodss.refresh();
						}
					},1000)
				}
			},
			clear(){
				this.selectFoods.forEach( good => {
					good.count = 0;
					this.flag = !this.flag;
				});
			},
			show(){
				if(this.totalPrice >= this.seller.minPrice)
				alert(this.totalPrice + this.seller.deliveryPrice);
			}
		}
		
	}
</script>
<style lang="less" scoped>
	.shopcar{
		width: 7.5rem;
		height: .98rem;
		background-color: #141d27;
		position: fixed;
		bottom:0;
		left:0;
		z-index:6;
		color:rgba(255,255,255,0.4);
		display: flex;
		.content-left{
			z-index: 99;
			flex:1;
			background: #141d27;
			.icon-car{
				width: 1.12rem;
				height: 1.12rem;
				background: #2b343c;
				display: inline-block;
				position: relative;
				border-radius:50%;
				top: -.2rem;
				left: 0rem;
				margin:0 .24rem;
				box-sizing: border-box;
				border: .12rem solid #121d27;
				text-align: center;
				.logo{
					line-height: .88rem;
					&.goods{
						background: rgb(0,160,220)
					}
					.icon-gouwuche{
						font-size: .4rem;
						&.icon{
							color: rgb(255,255,255);
						}
					}
					background: #2b343c;
					width: 100%;
					border-radius:50%;
					height: 100%;
				}
				.num{
					position: absolute;
					top:-.12rem;
					right: -.12rem;
					width: .48rem;
					height: .36rem;
					color: #fff;
					line-height: .36rem;
					background-color: rgb(240,20,20);
					border-radius: 40%;
				}
			}
			.total-price{
				display: inline-block;
				padding-right: .24rem;
				font-size: .32rem;
				margin-top: .3rem;
				vertical-align: top;
				color: rgba(255,255,255,0.4);
				border-right: 1px solid rgba(255,255,255,0.1);
				&.price{
					color:rgb(255,255,255)
				}
			}
			.delivery-price{
				margin-left: .2rem;
				font-size: .24rem;
				margin-top: .3rem;
				vertical-align: top;
				line-height: .36rem;
				z-index: 999;
				color: rgba(255,255,255,0.4);
				display: inline-block;
			}
		}
		.content-right{
			flex:0 0 2.1rem;
			color: rgba(255,255,255,0.4);
			background: #2b343c;
			font-weight: 700;
			font-size: .24rem;
			line-height: .98rem;
			text-align: center;
			.min-price{
				&.pay{
					background-color: rgb(0,160,220);
					color: rgb(255,255,255);
				}
			}
			
		}
		.goods-list{
			position: fixed;
			left: 0;
			bottom: .98rem;
			max-height: 5.13rem;
			width: 7.5rem;
			color: rgb(7,17,27);
			z-index: -11;
			background-color: #fff;
			.list-title{
				font-size: .28rem;
				color: rgb(7,17,27);
				line-height: .8rem;
				background-color: #f3f5f7;
				display: flex;
				justify-content:space-between;
				padding: 0 .36rem;
				border-bottom: 1px solid rgba(7,17,27,0.1);
				.title{
					color: rgb(7,17,27);
					font-weight: 200;
				}
				.clear{
					color: rgb(0,160,220);
				}
			}
			.foods-list{
				padding: 0 .36rem;
				max-height: 4.3rem;
				overflow: hidden;
				.food-list{
					display: flex;
					padding: .24rem 0;
					justify-content: space-between;
					border-bottom: 1px solid rgba(7,17,27,0.1);
					.title{
						font-size: .28rem;
						line-height: .48rem;
					}
					.control-num{
						.price{
							font-size: .28rem;
							color: rgb(240,20,20);
							line-height: .48rem;
							font-weight: 700;
							margin-right: .24rem;
						}
						display: flex;
					}
				}
			}
		}
		.top-enter-active,.top-leave-active{
			transition:all .5s
		}
		.top-enter,.top-leave-to{
			transform:translateY(100%);
		}
		.list-mask{
			position: fixed;
			top:0;
			left: 0;
			width: 7.5rem;
			height: 100%;
			backdrop-filter: blur(10px);
			z-index: -12;
			background-color: rgba(7,17,27,0.6);
		}
	}
</style>


