<template>
	<transition name="fade">
	<div class="food" v-show="showFlag" ref="food">
		<div class="food-detail">
			<div class="image-header">
				<img :src="foods.image" alt="">
				<span class="iconfont icon-shiliangzhinengduixiang4" @click="hideDetail"></span>
			</div>
			<div class="content">
				<h1 class="title">{{foods.name}}</h1>
				<div class="detail">
					<span>月售{{foods.sellCount}}份</span>
					<span>好评率{{foods.rating}}%</span>
				</div>
			</div>
			<div class="price-wrapper">
				<div>
					<span class="price">￥{{foods.price}}</span>
					<span v-show="foods.oldPrice" class="oldprice">￥{{foods.oldPrice}}</span>
				</div>
				<div class="shopcart">
					<!-- <span class="addcart">加入购物车</span> -->
					<cartcontrol :food="foods"></cartcontrol>
				</div>
			</div>
			<Split></Split>
			<div class="introduce">
				<h1 class="title">商品信息</h1>
				<p class="text">{{foods.info}}</p>
			</div>
			<Split></Split>
			<div class="ratings">
				<selectRating></selectRating>
				<div class="select"></div>
				<div class="rating-info">
					<ul>
						<li class="rating-item" v-for="(rating,index) in foods.ratings">
							<div class="item-info">
								<span class="ratetime">{{rating.rateTime | date}}</span>
								<div>									
									<span class="username">{{rating.username}}</span>
									<img class="usernameimg" :src="rating.avatar" alt="">
								</div>
							</div>
							<div class="fav">
								<i :class="classMap[rating.rateType]"></i>
								<span>{{rating.text}}</span>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
	</transition>
</template>
<script>
	import {mapGetters} from 'vuex'
	import Split from '../split/split.vue'
	import cartcontrol from '../cartcontrol/cartcontrol.vue'
	import BScroll from 'better-scroll'
	import selectRating from '../ratingselect/ratingselect.vue'
	export default{
		computed:{
			...mapGetters([
				'showFlag',
				'foods'
			])
		},
		watch:{
			foods(){
				this.$nextTick(()=>{
					setTimeout(()=>{
						if( !this.foodsd ) { 
							this.foodsd = new BScroll (this.$refs.food ,{
								click : true
							})
						}else{
							this.foodsd.refresh();
						}
					},1000)
				})
			}
		},
		created(){
			this.classMap=['iconfont icon-damuzhion','iconfont icon-down'];
		},
		methods:{
			hideDetail(){
				this.$store.dispatch('hideFood');
			}
		},
		components:{
			Split,
			selectRating,
			cartcontrol
		},
		filters:{
			date:function(value){
				function toDouble(n){
					return n<10?'0'+n: n;
				}
				var d=new Date(value);
				return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+' '+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
			}
		}
	}
</script>
<style lang="less" scoped>
	.fade-enter-active{
		transition: all .5s ease;
	}
	.fade-leave-active{
		transition: all .5s ease;
	}
	.fade-enter{
		transform:translateX(100%);
	}
	.fade-leave-to{
		transform:translateX(100%);
	}
	.food{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #fff;
		z-index: 5;
		overflow: hidden;
		.food-detail{
			.image-header{
				width: 100%;
				height: 0;
				position: relative;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					top:0;
					left:0;
				}
				span{
					width: .3rem;
					height: .3rem;
					color: rgba(255,255,255,0.6);
					position: absolute;
					left: .1rem;
					padding: .1rem;
					top: .1rem;
				}
			}
			.content{
				margin:.36rem;
				.title{
					font-size: .28rem;
					font-weight: 700;
					color:rgb(7,17,27);
					line-height: .28rem;
				}
				.detail{
					font-size: .2rem;
					margin: .16rem 0;
					line-height: .2rem;
					color: rgb(147,153,159);
				}
			}
			.price-wrapper{
				margin:.36rem;
				display: flex;
				justify-content:space-between;
				.shopcart{
					.addcart{
						width: 1.48rem;
						height: .48rem;
						color: #fff;
						background-color: rgb(0,160,220);
						border-radius: 24px;
						padding: .12rem .24rem;
					}
				}
				
				.price{
					color:rgb(240,20,20);
					font-weight: 700;
					font-size: .28rem;
					line-height: .48rem;
				}
				.oldprice{
					font-size: .2rem;
					font-weight: 700;
					color: rgb(147,153,159);
					line-height: .48rem;
					text-decoration: line-through;
				}
			}
			.introduce{
				padding: .36rem;
				.title{
					font-size: .28rem;
					font-weight: 700;
					color:rgb(7,17,27);
					line-height: .28rem;
					margin-bottom: .12rem;
				}
				.text{
					font-size: .2rem;
					font-weight: 200;
					color:rgb(77,85,93);
					line-height: .48rem;
				}

			}
			.ratings{
				.rating-info{
					padding: 0 .36rem;
					.rating-item{
						padding: .32rem 0;
						.item-info{
							display: flex;
							justify-content: space-between;
							.usernameimg{
								width: .24rem;
								height: .24rem;
								border-radius: 50%;
								vertical-align: top;
							}
							.username{
								font-size: .2rem;
								line-height: .24rem;
								vertical-align: top;
								color: rgb(147,153,159);
							}
							.ratetime{
								font-size: .2rem;
								color: rgb(147,153,159);
								line-height: .24rem;
								margin-bottom: .12rem;
							}
						}
						.fav{
							.icon-damuzhion{
								color: rgb(0,160,220);
							}
							.icon-down{
								color: rgb(183,187,191);
							}
						}
					}
				}
			}
		}
	}
</style>


