<template>
	<div class="goods">
		<div class="menu-wrapper" ref="menuWrapper">
			<ul>
				<li @click="selectMenu(index,$event)" v-for="(v,index) in goods" class="menu-item" :class="{'current':index===currentIndex}">
					<span><i v-if="v.type>0" class="icon" :class="classMap[v.type]"></i>{{v.name}}</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrapper" ref="foodWrapper">
			<div class="foodswrapper">
				<ul class="foods-item" v-for="(good,index) in goods" ref="foodList">
					<li class="goods-name">{{good.name}}</li>
					<li class="foods-name" v-for="(v,i) in good.foods">
						<div class="foods-info">
							<img @click="showDetail(good.foods[i],$event)" class="foods-img" :src="v.image" alt="">
							<div class="foods-content">
								<h1>{{v.name}}</h1>
								<p>{{v.description}}</p>
								<p><span>月售{{v.sellCount}}份</span><span>好评{{v.rating}}%</span></p>
								<div class="foods-number">
									<div>										
										<span class="price">¥{{v.price}}</span>
										<span class="oldprice" v-show="v.oldPrice">¥{{v.oldPrice}}</span>
									</div>
									<div>
										<cartcontrol :food="good.foods[i]"></cartcontrol>
									</div>
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
		<Food></Food>
		<shop-car></shop-car>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import BScroll from 'better-scroll'
	import shopCar from '../shopcar/shopcar.vue'
	import Food from '../food/food.vue'
	import cartcontrol from '../cartcontrol/cartcontrol.vue'

	export default{
		data(){
			return{
				listHeight:[],
				scrollY:0
			}
		},
		computed:{
			...mapGetters([
				'goods'
			]),
			currentIndex(){
				let foodList = this.$refs.foodList;
				let height = 0;
				for(let i=0;i<this.listHeight.length;i++){
					let height1 = this.listHeight[i];
					let height2 = this.listHeight[i+1];
					if(this.scrollY>=height1 && this.scrollY<height2){
						return i
					}

				}
				return 0;
			}
		},
		components:{
			Food,
			cartcontrol,
			shopCar
		},
		created(){
			this.$store.dispatch('getGoods');
			this.classMap=['decrease','discount','special','invoice','guarantee'];
			this.$nextTick(()=>{
				setTimeout(()=>{
					this._initmenu();
					this._initscroll();
					this._calcHeight()
				},100);
			})
		},
		methods:{
			showDetail(value,e){
				if(!e._constructed) return;
				this.$store.dispatch('showFood',value);
			},
			selectMenu(key,e){
				if(!e._constructed) return;
				let foodList =this.$refs.foodList;
				let el=foodList[key];
				this.contentwrap.scrollToElement(el,300);
			},
			_initmenu(){
				this.menuwrap = new BScroll ( this.$refs.menuWrapper ,{
					click : true
				});
			},
			_initscroll(){
				this.contentwrap = new BScroll ( this.$refs.foodWrapper ,{
					click : true,
					probeType:3
				});
				this.contentwrap.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
				})
			},
			_calcHeight(){
				let foodList =this.$refs.foodList;
				let height = 0;
				this.listHeight.push(height);
				for(let i =0;i<foodList.length;i++){
					height +=foodList[i].clientHeight;
					this.listHeight.push(height);
				}
				console.log(this.listHeight);
			}
		}
	}
</script>
<style lang="less" scoped>
	.goods{
		position: absolute;
		top:3.5rem;
		left: 0;
		width: 7.5rem;
		display:flex;
		height: 8.92rem;
		overflow: hidden;
		.menu-wrapper{
			flex:0 0 1.6rem;
			font-size: .24rem;
			color:rgb(77,85,93);
			.menu-item{
				&.current{
					background: #fff;
				}
				box-sizing: border-box;
				background: #f3f5f7;
				height: 1.08rem;
				padding: .2rem;
				display: flex;
				align-items:center;
				border-bottom:1px solid rgba(7,17,27,0.1);
				.icon{
				display: inline-block;
				width: .32rem;
				height: .32rem;
				vertical-align: middle;
				margin-top: -.1rem;
				&.decrease{
					background: url('../../../static/imgs/decrease.png');
					background-size: cover;
				}
				&.discount{
					background: url('../../../static/imgs/discount.png');
					background-size: cover;
				}
				&.guarantee{
					background: url('../../../static/imgs/guarantee.png');
					background-size: cover;
				}
				&.special{
					background: url('../../../static/imgs/special.png');
					background-size: cover;
				}
				&.invoice{
					background: url('../../../static/imgs/invoice.png');
					background-size: cover;
				}
			}
			}
		}
		.foods-wrapper{
			position: relative;
			box-sizing: border-box;
			flex:1;
			.foods-item{
				width: 5.9rem;
				.goods-name{
					border-left: .05rem solid #d9dde1;
					padding-left: .28rem;
					color: rgb(147,153,159);
					font-size: .24rem;
					line-height: .52rem;
					background: #f3f5f7;
				}
				.foods-name{
					margin: .36rem;
					border-bottom:1px solid rgba(7,17,27,0.1);
					.foods-info{
						padding-bottom: .36rem;
						display: inline-block;
						.foods-img{
							width: 1.16rem;
							height: 1.16rem;
							vertical-align: top;
						}
						.foods-content{
							display: inline-block;
							margin: .04rem 0 0 .2rem;	
							width: 3.7rem;						
							img{
								width: .4rem;
								height: .4rem;
							}
							.foods-number{
								display: flex;
								justify-content:space-between;
								.price{
									color: rgb(240,20,20);
									font-size: .28rem;
									font-weight: 700;
								}
								.oldprice{
									color: rgb(147,153,159);
									font-size: .2rem;
									font-weight: 200;
									text-decoration: line-through;
								}
								div{
									line-height: .4rem;
									input{
										width: .48rem;
										height: .48rem;
										font-weight:500;
										vertical-align: top;
										text-align: center;
										color:rgb(147,153,159);
										font-size: .2rem;
										line-height: .48rem;
									}
								}
							}
							h1{
								font-size: .28rem;
								color: rgb(7,17,27);
								line-height: .28rem;
							}
							p{
								margin: .16rem 0;
								font-size: .2rem;
								color: rgb(147,153,159);
								span{
									margin-right: .24rem;
								}
							}
						}
					}
				}
			}
			
		}
	}
</style>


