import types from '../types.js'
import axios from 'axios'

//data
const state = {
	goods:[],
	foods:{},
	showFlag:false
}

//返回数据
const getters = {
	goods(state){
		return state.goods;
	},
	foods(state){
		return state.foods;
	},
	showFlag(state){
		return state.showFlag
	}
}

//接受方法给mutaions执行
const actions = {
	getGoods({commit,state}){
		axios.get('../../../static/data.json').then((res)=>{

			if(res.data.errno === 0){
				commit(types.GET_GOODS,res.data.goods);
			}
		});
	},
	showFood({commit},value){
		commit(types.SHOW_FOOD,value);
	},
	hideFood({commit}){
		commit(types.HIDE_FOOD);
	}
}

const mutations = {
	[types.HIDE_FOOD](state){
		state.showFlag=false;
	},
	[types.SHOW_FOOD](state,value){
		state.foods = value;
		state.showFlag=true;
	},
	[types.GET_GOODS](state,data){
		state.goods=data
	}
}

export default{
	state,
	getters,
	actions,
	mutations
}


