import types from '../types.js'
import axios from 'axios'

//data
const state = {
	seller:{},
	detailShow:false,
	collectImg:false,
	collectInfo:"收藏"
}
//返回数据
const getters = {
	seller(state){
		return state.seller;
	},
	detailShow(state){
		return state.detailShow;
	},
	collectImg(state){
		return state.collectImg;
	},
	collectInfo(state){
		return state.collectInfo;
	}

}

//接受方法提交给mutations执行
const actions = {
	getSeller({commit,state}){
		axios.get('../../../static/data.json').then((res)=>{
			if(res.data.errno===0){
				commit(types.GET_SELLER,res.data.seller);
			}
		});
	},
	showSellerInfo({commit}){
		commit(types.SHOW_SELLER_DETAILINFO);
	},
	hideSellerInfo({commit}){
		commit(types.HIDE_SELLER_DETAILINFO);
	},
	getCollect({commit}){
		commit(types.COLLECT_SELLER);
	}
}

const mutations={
	[types.GET_SELLER](state,data){
		state.seller=data;
	},
	[types.SHOW_SELLER_DETAILINFO](state){
		state.detailShow = true;
	},
	[types.HIDE_SELLER_DETAILINFO](state){
		state.detailShow = false;
	},
	[types.COLLECT_SELLER](state){
		state.collectImg =  !state.collectImg;
		if(state.collectImg)
			state.collectInfo = "已收藏" ;
		else
			state.collectInfo = "收藏" ;	
	}
}

export default{
	state,
	getters,
	actions,
	mutations
}


