<template>
	<div class="ratingselect">
		<h1 class="title">商品评价</h1>
		<div class="rating-type">
			<span @click="select(2)" class="block positive" :class="{'active':2===selectType}">{{types[0]}}{{ratings.length}}</span>
			<span @click="select(0)" class="block positive" :class="{'active':0===selectType}">{{types[1]}}{{positiveCount}}</span>
			<span @click="select(1)" class="block negative" :class="{'active':1===selectType}">{{types[2]}}{{negativeCount}}</span>
		</div>
		<div class="haveimg" @click="toggle" :class="{'on':onlyContent}">
			<i class="iconfont icon-duihao"></i>
			<span class="includeinfo">只看有内容的评价</span>
		</div>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	export default{
		computed:{
			...mapGetters([
				'ratings',
				'onlyContent',
				'selectType',
				'types'
			]),
			positiveCount(){
				return this.ratings.filter(rating=>{
					return rating.rateType==0;
				}).length
			},
			negativeCount(){
				return this.ratings.filter(rating=>{
					return rating.rateType==1;
				}).length
			}
		},
		created(){
			this.$store.dispatch('getRatings');
		},
		methods:{
			select(type){
				this.$store.dispatch('showRating',type);
			},
			toggle(){
				this.$store.dispatch('toggles')
			}
		}
	}
</script>
<style lang="less" scoped>
	.ratingselect{
		padding: .36rem;
		border-bottom: 1px solid rgba(7,17,27,0.1);
		.title{
			color:rgb(7,17,27);
			font-size: .3rem;	
			margin-bottom: .16rem;
		}
		.rating-type{
			padding-bottom: .36rem;
			border-bottom: 1px solid rgba(7,17,27,0.1);
			.block{
				display: inline-block;
				padding: .16rem .24rem;	
				line-height: .32rem;
				color: rgb(77,85,93);
				&.active{
					color:#fff;
				}
				&.positive{	
					background-color: rgba(0,160,220,0.2);
					margin-right: .1rem;
					&.active{
						background-color: rgb(0,160,220);
					}
				}		
				&.negative{
					background-color: rgba(77,85,93,0.2);
					&.active{
						background-color: rgb(77,85,93);
					}
				}	
			}
		}
		.haveimg{
			padding-top: .24rem;
			&.on{
				.icon-duihao{
					color: rgb(0,160,220);
				}
			}
		}
	}
</style>


